body {
    background: #cfd8dc;
  }

  .customInput {
    outline: none;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    padding: 5px 5px;
    margin: 0 1%;
    font-size: 25px;
    color: #FFF;
    text-shadow: 0 2px 1px rgba(0, 0, 0, 0.5);
    text-align: center;
    width: 23%;
    background: rgba(255, 255, 255, 0.1);
    border-radius: 5px;
    border: 0;
   font-family: none;
  }
  .customInput input:invalid {
    background: rgba(135, 30, 30, 0.33);
  }
  .customInput input:focus {
    background: rgba(255, 255, 255, 0.05);
    outline: none;
  }
  
  .number {
    padding: 10px 0px;
    text-align: center;
  }
 
  .number:after {
    font-size: 18px;
    position: absolute;
    bottom: -30px;
    left: 3px;
  }
  

  .card-container{
    box-shadow: 5px 5px 10px 10px #312c2c5c;
      height: 250px;
      width: 500px;
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      margin: auto;
      background-color: #050604ed;
      border-radius: 20px;
      padding: 20px;
      background-image: url(../../../background.png);
  }

  .bank-name{
    font-weight: 900;
    font-size: 35px;
    height: 50px;
    text-align: right;
    color: white;
    font-family: none;
  }

  .chip-img{
      height: 70px;
      width: 80px;
  }
  .card-holder{
      height: 50px;
      font-size: 35px;
      font-weight: 900;
      color: white;
      font-family: none;
  }
  .card-exp{
    height: 30px;
    font-size: 20px;
    font-weight: 700;
    color: white;
    font-family: none;
  }
.background{
    background-color: #e5e7ec;
    position: absolute;
    width: 100%;
    height: 100%;
    background-image: url(../../../background.png);
    background-repeat: no-repeat;
    background-size: cover;

}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}
  @media screen and (max-width: 600px) {
    .card-container{
        margin: auto 30px auto 30px;
        width: auto;
        height: 210px;
    }
    .bank-name{
        font-size: 20px;
    }
    .card-exp{
        font-size: 15px;
    }
    .card-holder{
        font-size: 20px;
    }
    .customInput{
        font-size: 18px;
    }
    .chip-img{
        height: 50px;
        width: 60px;
    }
  }